import React, { Component } from 'react';
import Clock from '../Clock/Clock';
import "./FormHello.css";


export class FormHello extends Component{
	continue = e => {
		e.preventDefault();
		this.props.nextStep();
	}
	render() {
		const { values, handleChange } = this.props;
		return (

				<div className="main">
					<h1><Clock /></h1>
					<div className="main-information">
						<h2>Hello,what's your name:</h2>
						<input type="text" className="form-control" placeholder="Enter your name" 
						onChange={handleChange('firstName')} defaultValue={values.firstName} id="standard-textarea" />
						<button onClick={this.continue} className="btn btn-primary" type="submit" >Confirm</button>
						</div>
					</div>
				
		)
	}
}






export default FormHello;
