import React, { Component } from "react";
import "./ListToDo.css";



export class FormAddTask extends Component{

	constructor(props){


		super(props);

		this.newIndex = 2;

	}



createNewTask(e){

	if (e.key === 'Enter' &&  !e.currentTarget.value === !'undefined') {
	const newTask = { title: e.currentTarget.value, isDone: false, id: this.newIndex};

		this.props.onCreate(newTask);
		
		e.currentTarget.value = '';
		this.newIndex++;
	}
}

render() {
	return (
			<div className="header">
				<input onKeyPress={this.createNewTask.bind(this)} placeholder="For example, learn js" className="input-todo"/>
			</div>
			
	); 
}

	
}
export default FormAddTask;