import React, { Component } from "react";
import "./ListToDo.css";


export class Task extends Component{

	constructor(props){
		super();
		this.state = {
		task: props.task
		};
		this.parentDelete = props.delete;
	}


deleteTask(e){
	this.parentDelete(this.state.task.id);
}




toogleTaskIsChecked(e) {

		var newTask = {
			...this.state.task,
			isDone: !this.state.task.isDone
		};
		
		this.setState({
			task: newTask
		});
	
}
render() {
	return (
		
		<div key={this.state.task.title} className={ this.state.task.isDone ? 'task done' : 'task'}>
		<input type="checkbox" checked={this.state.task.isDone} onClick={this.toogleTaskIsChecked.bind(this)}className="input-checked-task"/>
		{this.state.task.title} 
		<span className="delete" onClick={this.deleteTask.bind(this)}>x</span>
		</div>
	); 
}





	
}
export default Task;